*,
*:before,
*:after {
    box-sizing: border-box;
}

body {
    font-family: 'Lato', 'Roboto', sans-serif;
    line-height: 1.5;
}

img {
    display: block;
    max-width: 100%;
    height: auto;
    object-fit: cover;
}

a {
    display: block;
    text-decoration: none;
}

p {
    margin: 0;
}

span {
    vertical-align: top;
}